<template>
  <div id="distributionRecord">
    <van-overlay :show="load" :z-index="10000" class="loadBox" :lock-scroll="true">
      <van-loading class="load" type="spinner" color="#1989fa" :vertical="true" v-if="load" />
    </van-overlay>
    <div class="content">
      <header>
        累计收益：￥
        <span v-if="!totalLoad">{{total_income}}</span>
        <van-loading type="spinner" color="#c9c9c9" size="18" v-else />
      </header>
      <van-pull-refresh
        v-model="pullLoading"
        :success-text="successMessage"
        @refresh="pullRefresh"
        :disabled="disablePullRefresh"
      >
        <van-list
          v-model="pushLoading"
          :immediate-check="false"
          :finished="finished"
          finished-text="没有更多了"
          @load="onLoad"
          :error.sync="pushError"
          error-text="请求失败，点击重新加载"
        >
          <div class="distributionList">
            <div class="distributionBox" v-for="(item,index) in dataList" :key="index">
              <p>
                <span>订单编号</span>
                <span>{{item.no}}</span>
              </p>
              <p>
                <span>订单时间</span>
                <span>{{item.paid_at|submitTimeFormatType}}</span>
              </p>
              <p>
                <span>商品</span>
                <span>《{{item.program.name}}》</span>
              </p>
              <p>
                <span>订购人</span>
                <span>{{item.extra.from_user_name}}</span>
              </p>
              <p>
                <span>成单价格</span>
                <span>￥{{item.amount}}</span>
              </p>
              <p>
                <span>邀请奖励</span>
                <span>50%（￥50.00）</span>
              </p>
            </div>
          </div>
        </van-list>
      </van-pull-refresh>
    </div>
  </div>
</template>
<script>
import { getdistributionData } from "../assets/javascript/apis.js";
export default {
  data() {
    return {
      pullLoading: false,
      disablePullRefresh: false,
      getDataFlag: true,
      pushLoading: false,
      finished: false,
      dataList: [],
      page: 1,
      pushError: false,
      successMessage: "",
      load: false,
      total_income: "",
      totalLoad: true,
    };
  },
  created() {
    this.load = true;
    this.getData();
    //设置分享链接
    this.$shareConfig();
  },
  methods: {
    //获取页面数据
    getData(page, refreshData = true) {
      //控制发起请求的频率
      if (!this.getDataFlag) return;
      this.getDataFlag = false;
      //发起请求前禁用下拉刷新
      this.disablePullRefresh = true;

      const data = {
        page: page ? page : 1,
      };

      if (refreshData) {
        this.totalLoad = true;
      }

      getdistributionData()
        .then((res) => {
          console.log(res);
          //处理数据
          if (refreshData) {
            this.dataList = [];
          }
          this.dataList.push(...res.orders.data);
          this.page = res.orders.current_page;
          this.total_income = res.total_income;

          //可以重新发起请求
          this.getDataFlag = true;
          if (refreshData) {
            //展现下拉刷新成功提示信息
            this.successMessage = "刷新成功";
            // 加载数据成功，修改下拉刷新的状态
            this.pullLoading = false;
            //重新启用下拉刷新
            this.disablePullRefresh = false;
            //隐藏总收益部分加载图标
            this.totalLoad = false;
          } else {
            //加载数据成功后，修改上拉加载更多的状态
            this.pushLoading = false;
          }
          //当加载到最后一页的时候，禁用上拉加载更多功能
          if (res.orders.current_page >= res.orders.last_page) {
            this.finished = true;
          }
          this.load = false;
        })
        .catch((res) => {
          console.log(res);
          //可以重新发起请求
          this.getDataFlag = true;
          //隐藏掉加载图标
          this.load = false;
          this.totalLoad = false;
          if (refreshData) {
            //展现下拉刷新成功提示信息
            this.successMessage = "刷新失败，请重新刷新";
            // 加载数据成功，修改下拉刷新的状态
            this.pullLoading = false;
            //重新启用下拉刷新
            this.disablePullRefresh = false;
          } else {
            //加载数据失败后，修改上拉加载更多的状态
            this.pushLoading = false;
            //当请求失败时，展现上拉加载更多失败信息
            this.pushError = true;
          }
        });
    },
    //下拉加载
    pullRefresh() {
      this.getData();
    },
    //上拉加载更多
    onLoad() {
      this.page = this.page + 1;
      this.getData(this.page, false);
    },
  },
};
</script>
<style lang="scss" scoped>
#distributionRecord {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  .loadBox {
    background: white;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    header {
      width: 100%;
      height: 42px;
      text-align: center;
      line-height: 42px;
      background: rgba(75, 165, 245, 1);
      font-size: 14px;
      color: rgba(255, 255, 255, 1);
      position: fixed;
      top: 0;
      left: 0;
      z-index: 1000;
      display: flex;
      justify-content: center;
    }
    /deep/.van-pull-refresh {
      margin-top: 42px;
      flex-grow: 1;
      overflow: visible;
      display: flex;
      flex-direction: column;
      .van-pull-refresh__track {
        flex-grow: 1;
        height: auto;
        display: flex;
        flex-direction: column;
        .van-pull-refresh__head {
        }
      }
    }
    /deep/.van-list {
      flex-grow: 1;
    }
    .distributionList {
      width: 100%;
      padding: 0 15px;
      box-sizing: border-box;
      .distributionBox {
        width: 100%;
        height: 205px;
        margin-top: 15px;
        padding: 20px 15px;
        box-sizing: border-box;
        background: rgba(242, 244, 246, 1);
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        p {
          font-size: 14px;
          color: rgba(7, 7, 7, 1);
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }
}
</style>